// В этом файле работает София
.products{
    background: #fefefe;
} 

.products-text{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    padding-top: 80px;
    color: #FFA5BA;
}

.products-name{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-top: 15px;
    padding-bottom: 8px;
    color: #D41443;
}


.products__ice-cream__background{
    width: 280px;
    height: 489px;
    left: 20px;
    top: 802px;
    background: #FFB8CA;
    border-radius: 24px;
    margin-top: 140px;
}

.products__ice-coffee__background{
    width: 280px;
    height: 489px;
    left: 20px;
    top: 1431px;
    background: #F0D1A5;
    border-radius: 24px;
    margin-top: 140px;
}

.products__milkshakes__background{
    width: 280px;
    height: 489px;
    left: 20px;
    top: 2060px;
    background: #C2E297;
    border-radius: 24px;
    margin-top: 140px;
}

.ice-cream{
    height: 23px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FFFFFF;
}

.ice-cream::after{
    content: "...";
    display: block;
    padding-top: 32px;
}

.ice-cream__text{
    width: 200px;
    height: 124px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    padding-top: 70px;
}